/* === Global Color System - Navy  Gold Theme === */
:root {
  /* Core (Navy) */
  --bg:            #0B1220;  /*  (   ) */
  --surface:       #0F172A;  /* /   */
  --surface-2:     #111827;  /*     */
  --surface-3:     #1F2937;  /* /  */

  /* Text */
  --text:          #F3F4F6;  /*   ( ) */
  --text-sub:      #C7CDD8;  /*   */
  --muted:         #9AA3B2;  /* / */

  /* Brand (Gold accent)    */
  --gold:          #C8A74A;  /*   ( #d4af37  ) */
  --gold-strong:   #D4B34F;  /*   */
  --gold-20:       rgba(200,167,74,0.20);
  --gold-35:       rgba(200,167,74,0.35);

  /* States */
  --success:       #22C55E;
  --warning:       #EAB308;
  --danger:        #EF4444;

  /* Borders / Shadows / Rings */
  --border:        rgba(255,255,255,0.08);
  --ring:          rgba(200,167,74,0.35);
  --shadow-soft:   0 8px 24px rgba(0,0,0,0.35);

  /* Radii () */
  --radius-sm:     8px;
  --radius-md:     12px;
  --radius-lg:     16px;
}

@media (prefers-color-scheme: light) {
  /*       */
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}
